body {
  width: 400px;
  padding: 20px;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}

.container {
  text-align: center;
}

.file-input-container {
  margin-bottom: 15px;
}

.file-upload-btn {
  display: inline-block;
  padding: 10px 20px;
  background-color: #4285f4;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.file-upload-btn:hover {
  background-color: #3367d6;
}

.divider {
  margin: 15px 0;
  color: #666;
  font-size: 14px;
}

.drop-zone {
  border: 2px dashed #ccc;
  border-radius: 5px;
  padding: 30px;
  margin: 10px 0 20px;
  cursor: pointer;
  transition: all 0.3s;
  background-color: white;
}

.drop-zone.highlight {
  border-color: #4285f4;
  background-color: #e8f0fe;
}

.drop-zone p {
  margin: 0;
  color: #666;
}

.file-info {
  margin-top: 20px;
  text-align: left;
}

#file-name {
  margin: 0 0 10px 0;
  font-size: 16px;
  color: #333;
}

#file-content {
  white-space: pre-wrap;
  word-wrap: break-word;
  max-height: 300px;
  overflow-y: auto;
  border: 1px solid #ddd;
  padding: 10px;
  background-color: white;
  border-radius: 4px;
  font-family: monospace;
}

.clear-btn {
  margin-top: 10px;
  padding: 8px 15px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.clear-btn:hover {
  background-color: #d32f2f;
}
/* 상단 바 스타일 */
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  background-color: #f0f0f0;
  border-bottom: 1px solid #ddd;
}

/* 스위치 라벨 스타일 */
.switch-label {
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 6px;
}

/* Refresh 버튼 스타일 */
.refresh-btn {
  padding: 6px 12px;
  font-size: 14px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.refresh-btn:hover {
  background-color: #45a049;
}


.crashlytics-detection {
  color: #d32f2f;
  background-color: #ffebee;
  padding: 8px 12px;
  border-radius: 4px;
  margin-bottom: 15px;
  font-weight: bold;
  border-left: 4px solid #d32f2f;
}

.stack-translation-title {
  font-weight: bold;
  margin-top: 10px;
}

.stack-translation-result {
  margin-top: 5px;
  padding: 10px;
  background-color: #f5f5f5;
  border-radius: 4px;
  max-height: 300px;
  overflow-y: auto;
  font-family: monospace;
  white-space: pre-wrap;
}

.stack-translation-warning {
  color: #f44336;
  margin-top: 10px;
  font-weight: bold;
}

.stack-translation {
  color: #4285f4;
  font-style: italic;
  margin-left: 10px;
  font-size: 0.9em;
  display: inline-block;
  padding: 2px 5px;
  background-color: #f5f5f5;
  border-radius: 3px;
}

/* 로딩 오버레이 스타일 */
.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  display: none; /* 기본적으로 숨김 */
}

/* 로딩 스피너 스타일 */
.loading-spinner {
  border: 4px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top: 4px solid #4285f4;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

#hud-container {
  transition: all 0.3s ease;
  animation: fadeIn 0.5s;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
